<template>
  <div class="message-alert">
      <!-- classstatusbs4classmessages -->
    <div
      class="alert alert-dismissible"
      :class="'alert-' + item.status"
      v-for="(item, i) in messages"
      :key="i"
    >
    <!-- message -->
      {{ item.message }}
      <!-- removeMessagemessagesiiv-forindex -->
      <button
        type="button"
        class="close"
        @click="removeMessage(i)"
        aria-label="Close"
      >
        <span aria-hidden="true">&times;</span>
      </button>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      //messages
      messages: []
    };
  },
  methods: {
    //functionmessagestatus
    updateMessage(message, status) {
      //new Date()timestamp
      const timestamp = Math.floor(new Date() / 1000);
      //timestamppushdatamessages
      this.messages.push({
        message,
        status,
        timestamp
      });
      //functiontimestamp
      this.removeMessageWithTiming(timestamp);
    },
    //functioni(index)
    removeMessage(num) {
      //datamessages1
      this.messages.splice(num, 1);
    },
    //funciton
    removeMessageWithTiming(timestamp) {
      const vm = this;
      //50005function
      setTimeout(() => {
        //forEachmessages
        vm.messages.forEach((item, i) => {
          //timestampindex1
          if (item.timestamp === timestamp) {
            vm.messages.splice(i, 1);
          }
        });
      }, 5000);
    }
  },
  //created$bus
  created() {
    const vm = this;
    //$on()message:pushmessagestatusstatuswarning
    vm.$bus.$on("message:push", (message, status = "warning") => {
      //functionmessagesfunction
      console.log("");
      vm.updateMessage(message, status);
    });
  }
  //$bus.$emit
};
</script>

<style scope>
.message-alert {
  position: fixed;
  max-width: 50%;
  /* top: 56px;
  right: 20px; */
  top: calc(100vh - 60vh);
  left:calc(50vw - 5%);
  z-index: 1100;
}
</style>


